import React, { useState, useEffect } from "react";
import logo from "./logo.svg";
import "./App.css";
import { db } from "./FirebaseConfig";
// Bootstrap CSS
import "bootstrap/dist/css/bootstrap.min.css";
// Bootstrap Bundle JS
import "bootstrap/dist/js/bootstrap.bundle.min";
import Table from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import FloatingLabel from "react-bootstrap/FloatingLabel";
import Navbars from "./Navbar";

import { uid } from "uid";
import {
  onValue,
  ref,
  set,
  get,
  off,
  remove,
  update,
  onChildAdded,
  onChildChanged,
} from "firebase/database";

function App() {
  //function
  // const [bookName, setBook] = useState("");
  // const [type, setType] = useState("");
  // const [description, setDescript] = useState("");

  const [formData, setFormData] = useState({
    bookName: "",
    type: "",
    description: "",
  });
  const [datas, setDatas] = useState([]);
  const [loading, setLoading] = useState(true);
  const [isEdit, setIsEdit] = useState(false);
  const [tempUuid, setTempUuid] = useState("");

  //read

  useEffect(() => {
    // Fetch initial data
    const fetchData = async () => {
      const snapshot = await get(ref(db, "books"));
      if (snapshot.exists()) {
        const data = snapshot.val();
        const dataArray = Object.values(data);
        setDatas(dataArray);
      }
      setLoading(false);
    };

    fetchData();

    // Listen for new data being added
    const booksRef = ref(db, "books");
    onChildAdded(booksRef, (snapshot) => {
      const newData = snapshot.val();
      setDatas((prevDatas) => [...prevDatas, newData]);
    });

    // Listen for data being changed
    onChildChanged(booksRef, (snapshot) => {
      const changedData = snapshot.val();
      setDatas((prevDatas) =>
        prevDatas.map((data) => {
          if (data.uuid === changedData.uuid) {
            return changedData;
          } else {
            return data;
          }
        })
      );
    });

    // Clean up listeners on unmount
    return () => {
      off(booksRef, "child_added");
      off(booksRef, "child_changed");
    };
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
  };

  //write
  const writeDb = (event) => {
    event.preventDefault();
    const uuid = uid();
    set(ref(db, "books/" + uuid), {
      uuid,
      bookName: formData.bookName,
      type: formData.type,
      description: formData.description,
    });

    setFormData({
      bookName: "",
      type: "",
      description: "",
    });
  };

  //delete
  const deleteDb = (uuid) => {
    const bookRef = ref(db, "books/" + uuid);
    remove(bookRef);
    setDatas((prevDatas) => prevDatas.filter((data) => data.uuid !== uuid));
  };

  //get-info
  const handleUpdate = (formData) => {
    setIsEdit(true);
    setTempUuid(formData.uuid);
    setFormData({
      bookName: formData.bookName,
      type: formData.type,
      description: formData.description,
    });
  };
  //update
  const handleSubmitChange = (e) => {
    e.preventDefault();
    update(ref(db, "books/" + tempUuid), {
      bookName: formData.bookName,
      type: formData.type,
      description: formData.description,
    });

    setFormData({
      bookName: "",
      type: "",
      description: "",
    });
    setIsEdit(false);
  };

  // Your web app's Firebase configuration
  return (
    <>
      <Navbars />
      <div className="App container bg-light my-5 shadow  bg-body-tertiary rounded">
        <>
          <div className="container mt-3 pt-3">
            <Table striped bordered>
              <thead>
                <tr>
                  <th>Book Name</th>
                  <th>Type</th>
                  <th>Description</th>
                  <th>Submit</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>
                    <FloatingLabel
                      controlId="floatingInput"
                      label="Book Name"
                      className="mb-3"
                    >
                      <Form.Control
                        name="bookName"
                        type="text"
                        placeholder="something..."
                        value={formData.bookName}
                        onChange={handleChange}
                        autocomplete="off"
                      />
                    </FloatingLabel>
                  </td>
                  <td>
                    <FloatingLabel
                      controlId="floatingInput"
                      label="Type"
                      className="mb-3"
                    >
                      <Form.Control
                        type="text"
                        name="type"
                        placeholder="something..."
                        value={formData.type}
                        onChange={handleChange}
                        autocomplete="off"
                      />
                    </FloatingLabel>
                  </td>
                  <td>
                    <FloatingLabel
                      controlId="floatingTextarea2"
                      label="Description"
                    >
                      <Form.Control
                        as="textarea"
                        name="description"
                        placeholder="Leave a comment here"
                        value={formData.description}
                        onChange={handleChange}
                        autocomplete="off"
                      />
                    </FloatingLabel>
                  </td>
                  <td>
                    {isEdit ? (
                      <>
                        <Button
                          onClick={handleSubmitChange}
                          variant="success"
                          className="me-2"
                        >
                          Update
                        </Button>
                        <Button
                          variant="danger"
                          onClick={() => {
                            setIsEdit(false);
                            setFormData({
                              bookName: "",
                              type: "",
                              description: "",
                            });
                          }}
                        >
                          X
                        </Button>
                      </>
                    ) : (
                      <Button type="submit" onClick={writeDb} variant="success">
                        Submit
                      </Button>
                    )}
                  </td>
                </tr>
              </tbody>
            </Table>
          </div>
        </>
        <br />
        {loading ? (
          <p>Loading data...</p>
        ) : (
          <div className="container pb-2">
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th>UUID</th>
                  <th>Book Name</th>
                  <th>Type</th>
                  <th>Description</th>
                  <th>Update</th>
                  <th>Delete</th>
                </tr>
              </thead>
              <tbody>
                {datas.map((data) => (
                  <tr key={data.uuid}>
                    <td>{data.uuid}</td>
                    <td>{data.bookName}</td>
                    <td>{data.type}</td>
                    <td>{data.description}</td>
                    <td>
                      <Button
                        onClick={() => handleUpdate(data)}
                        variant="success"
                      >
                        Update
                      </Button>
                    </td>
                    <td>
                      <Button
                        onClick={() => deleteDb(data.uuid)}
                        variant="danger"
                      >
                        Delete
                      </Button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </Table>
          </div>
        )}
      </div>
    </>
  );
}

export default App;
